export default {
  data () {
    return {
      // 支付凭证
      PayVoucherNumber: ''
    }
  },
  methods: {
    // 去支付结果页面
    goPaymentResult () {
      this.$http.post('/api/WeiXinPay.ashx', {
        action: 'Pay',
        voucherNumber: this.PayVoucherNumber,
        OpenId: uni.getStorageSync('openid')
      }).then(res => {
        uni.requestPayment({
          provider: 'wxpay',
          timeStamp: res.data.timeStamp,
          nonceStr: res.data.nonceStr,
          package: 'prepay_id=' + res.data.prepay_id,
          signType: 'MD5',
          paySign: res.data.paySign,
          success: (res) => {
            console.log(res)
            if (res.errMsg == 'requestPayment:ok') {
              uni.reLaunch({
                url: '/pages/paymentResult/paymentResult'
              })
            } else {
              console.log('支付失败')
            }
          },
          fail: (err) => {
            uni.reLaunch({
              url: '/pages/orderList/orderList?active=' + 0
            })
          }
        });
      })
      // uni.reLaunch({
      //   url: '/pages/paymentResult/paymentResult'
      // });
    }
  },
  onLoad (options) {
    console.log(options)
    this.PayVoucherNumber = options.PayVoucherNumber
    console.log(this.PayVoucherNumber)
  }
}
